@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kalam:wght@700&display=swap');

 * {
     margin: 0;
     padding: 0;
     box-sizing: border-box;
}
 body {
     font-family: 'Montserrat', sans-serif;
    /* background: #1d1f30;
     */
     background-color: #E9E3E6;
     z-index: 0;
     color: #2E2532;
     overflow-x: hidden;
}
 header {
     padding: 50px 15px 0px;
     text-align: center;
}
 header h1 {
  font-family: 'Kalam', cursive;
     color: #EF476F;
     font-size: 48px;
     font-weight: 900;
     font-style: italic;
     text-transform: uppercase;
  width: 100%;

  text-shadow:  -1px -1px 0 #3e2e6a, 1px -1px 0 #3e2e6a, 4px 0px 0 #3e2e6a, 4px 1px 0 #3e2e6a;
}


  main {
     width: 100%;
     max-width: 990px;
     margin: 0 auto;
}
 section {
     padding: 15px;
}
 .searchbox {
     display: block;
     width: 100%;
     padding: 15px;
     border: none;
     outline: none;
     background: none;
     background-color: #eee;
     border-radius: 8px;
     color: #53565a;
     font-size: 20px;
     font-weight: 300;
     transition: 0.4s ease-out;
}
 .searchbox:focus {
     box-shadow: 0px 0px 2px 1px #EF476F;
}
 .results {
     display: flex;
     flex-wrap: wrap;
     margin: 0px -15px;
     justify-content: center;
}
 .results .result {
     width: 25%;
     min-width: 330px;
     padding: 15px;
     display: flex;
     flex-direction: column;
     cursor: pointer;
}
 .results .result img {
     display: block;
     width: 100%;
     height: 284px;
     object-fit: cover;
     border-radius: 20px 20px 0px 0px;
     -webkit-box-shadow: 2px 0px 7px 0px rgba(136, 133, 153, 1);
     -moz-box-shadow: 2px 0px 7px 0px rgba(136, 133, 153, 1);
     box-shadow: 2px 0px 7px 0px rgba(136, 133, 153, 1);
}
 .results .result h3 {
     top: 20px;
     left: 20px;
     text-transform: uppercase;
     padding: 10px;
     font-size: 13px;
}
 .result-box {
     color: black;
     background-color: #fff;
     border-radius: 0px 0px 20px 20px;
     height: fit-content;
}
 .first-line{
     display: flex;
     justify-content: space-between;
     align-items: baseline;
     width: 100%;
     padding-right: 1em;
}
 .results .result h3:hover {
     font-weight: 700;
     color: #EF476F;
}
 .popup {
     position: fixed;
     display: flex;
     justify-content: center;
     align-items: center;
     top: 0;
     left: 0;
     width: 100%;
     height: 105vh;
     background-color: #100f0fbb;
     z-index: 2;
     padding-bottom: 10px;
     color: #2E2532;
     overflow-y: scroll;
}
 .popup .content {
     display: block;
     width: 80%;
     height: 80%;
     overflow: scroll;
     background-color: #D9D3D6;
}
 html {
     overflow-x: initial !important;
}
 .popup .content h2 {
     font-size: 28px;
     font-weight: 600;
}
 .popup .content h2 span {
     font-weight: 300;
     font-size: 20px;
}
 .popup .content .rating {
     margin-bottom: 30px;
}
 .popup .content .plot {
     flex-wrap: wrap;
     margin: 0px -15px 30px;
}
 .popup .content .plot img {
     flex: 1 1 50%;
     max-width: 300px;
     padding: 10px 15px 0 15px;
}
 .popup .content .plot p {
     flex: 1 1 50%;
     padding: 15px;
}
 h5{
     font-size: 0.8em;
     padding-top: 1em;
}

 .value:last-child:after {
     content: ".";
}
 .popup .content .close {
     display: inline-block;
     position: relative;
     padding: 7px 18px;
     font-size: 18px;
     font-weight: 700;
     background: #EF476F;
     color: #fff;
     border-radius: 8px;
     border: none;
     outline: none;
     -webkit-appearance: none;
     appearance: none;
     cursor: pointer;
     transition: 0.4s ease-out;
}
 .close-box{
     text-align: center;
     padding-bottom: 5px;
}
 .anime-info{
     background-color: white;
     padding: 0.9em;
     border-radius: 16px;
     box-shadow: 0px 30px 30px -25px rgba(#4133B7, 0.25);
}
 .anime-info .item{
     margin: 5px 0;
}
 .anime-info .item-head{
     color: #2E2532;
}
 .popup .content .close:hover {
     background: #223343;
}
 .fa-star {
     color: #ffd700;
     padding: 0 10px;
     font-size: 12px;
     margin-top: 1em;
}
 span {
     color: #aeb1b6;
     font-size: 0.9em;
     margin-top: 1em;
}
 .movie-gen li, .label {
     font-family: "Open Sans", sans-serif;
     font-size: 12px;
     color: #acacac;
     width: auto;
     display: block;
     float: left;
     margin-right: 6px;
     font-weight: 600;
     display: block;
}
 .col_1 {
     margin: 0% 0.5% 0.5% 0.5%;
     display: block;
}
 .year {
     float: right;
     padding: 0 12px 0 0;
}
.no-result{
  text-align: center;
}
 @media only screen and (max-width: 768px) {
    /* For mobile phones: */
     .results .result {
         margin: auto;
    }

    .popup .content {
        display: block;
        width: 100vw;
        height: 100%;
        overflow: scroll;
