 * {
     box-sizing: border-box;
}
 *::before, *::after {
     box-sizing: border-box;
}
 body {
     line-height: 1.5;
     text-rendering: optimizeLegibility;
}
 ul {
     padding: 0;
     margin: 0;
     list-style: none;
}
 ul li {
     margin: 0 1.75rem 0 0;
}
 a {
     color: #888;
     text-decoration: none;
     transition: all 0.2s ease;
}
 a:hover {
     color: #333;
}
 a.active {
     color: #333;
}
 section {
     display: block;
}
 img {
     max-width: 100%;
     height: auto;
     -o-object-fit: cover;
     object-fit: cover;
}
 nav {
     display: block;
}
 nav li {
     font-size: 1.125rem;
     margin: 0;
}
 .flex-nav ul {
     position: absolute;
     z-index: 1;
     list-style: none;
     margin: 0;
     padding: 0;
     display: flex;
     flex-wrap: wrap;
     flex-direction: column;
     display: none;
     width: 100%;
     left: 0;
     padding: 1rem;
     background: #fff;
     text-align: center;
}
 .flex-nav ul.active {
     display: flex;
}
 .flex-nav ul li {
     margin: 0.5rem 0;
}
 #highlight {
     color: #333;
     font-size: 1.125rem;
     text-transform: uppercase;
}
 .breadcrumb-list {
     display: flex;
     flex-wrap: wrap;
     padding: 0;
     margin: 1rem 0 0 0;
     list-style: none;
}
 .breadcrumb-list li {
     font-size: 0.85rem;
     letter-spacing: 0.125rem;
     text-transform: uppercase;
}
 .breadcrumb-item.active {
     color: #333;
}
 .breadcrumb-item + .breadcrumb-item::before {
     content: "|";
     display: inline-block;
     padding: 0 0.5rem;
     color: #bbb8b8;
}

 .description {
     border-top: 0.0625rem solid #2e2534;
     padding: 1rem 0 0 0;
}
 ::-webkit-scrollbar {
     width: 10px;
}
/* Track */
 ::-webkit-scrollbar-track {
     border-radius: 10px;
}
/* Handle */
 ::-webkit-scrollbar-thumb {
     background: #EF476F;
     border-radius: 10px;
}
/* Handle on hover */
 ::-webkit-scrollbar-thumb:hover {
     background: #EF476F;
}
 .container {
     margin: auto;
     padding: 0 1rem;
     max-width: 75rem;
     width: 100%;
}
 .grid > [class*=column-] {
     padding: 1rem;
}
 .grid.menu, .grid.product {
     border-bottom: 0.0625rem solid #2e2534;
}
 .grid.menu > [class*=column-] {
     padding: 0.5rem 1rem 0.5rem 1rem;
}
 .grid.product {
     padding: 0 0 1.5rem 0;
}
 .grid.second-nav > [class*=column-] {
     padding: 0.5rem 1rem;
}
 footer {
     padding: 1rem 0;
     text-align: center;
}
 .image-list li {
     margin: 0;
}
 @media (min-width: 62rem) {
     .product-image img, .image-list img {
         width: 100%;
    }
     .popup{
         height: 100vh;
    }
     .product-image {
         display: block;
    }
     .product-image img {
         height: 52vh;
    }
     .product-image img.active {
         display: block;
         margin: 0 0 0.75rem 0;
    }
     .image-list {
         display: flex;
         overflow: hidden;
    }
     .image-list li {
         margin: 0 0.75rem 0 0;
         flex-basis: 100%;
    }
     .image-list li:nth-child(3) {
         margin: 0;
    }
     .image-list img {
         height: 10rem;
         width: 100%;
         transition: opacity 0.3s ease;
         cursor: pointer;
    }
     .image-list img:hover {
         opacity: 0.7;
    }
     nav ul {
         justify-content: flex-end;
    }
     .toggle-nav {
         display: none;
    }
     .flex-nav {
         display: block;
    }
     .flex-nav ul {
         display: flex;
         flex-direction: row;
         position: relative;
         justify-content: flex-end;
    }
     .flex-nav ul li {
         font-size: 1.125rem;
         margin: 0 1.5rem 0 0;
    }
     .flex-nav ul li:nth-child(4) {
         margin: 0;
    }
}
