    /* margin-left: 4px; */
    margin-right: 4px;
    padding: calc(0.2em - 1px) 0.9em;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    text-transform: uppercase;
}

.button-60:active {
  border-color: #4a4a4a;
  outline: 0;
}

.button-60:focus {
  border-color: #485fc7;
  outline: 0;
}

.button-60:hover {
  
  background-color: #ef476f;
  color: white;
  font-weight: bold;
}

.button-60:focus:not(:active) {
  box-shadow: rgba(72, 95, 199, .25) 0 0 0 .125em;
}
  
.close-button:hover{
    color: #2E2532;
    border:2 px solid #ef476f;
    overflow: hidden;
}